@import "index", "highlight";

@import "./YH.css";




.categories {
  color: #68f;
  margin-bottom: .15em; }









.icons svg {
  height: 1em;
  width: 1em;
  fill: currentColor;
  transition: .2s color;
  vertical-align: middle;
  margin-bottom: .15em; }







.archive {
  font-size: 1.1em; }

.archive time {
  display: inline-block;
  min-width: 10ch;
  margin: 0 .2em; }





.related span {
  display: block;
  color: reduce(100); }

.related a {
  font-size: 1.2em;
  font-weight: bold; }

.next {
  text-align: right; }





.title {
  font-size: 1.5em;
  width: 100%;
  margin: .1em 0 .6em; }





.icons a:hover svg {
  color: reduce(100); }

@media (min-width: 70em) {
  .large {
    margin: 1em -20%; }

  .large img {
    width: 100%; } }


